<template>
  <div class="chatTop">
    <!-- RoomKey入力モーダル -->
    <!-- roomModalのmountedを聞かせるためにv-ifを使用 -->
    <template v-if="isOpenModal">
      <roomModal
        id="roomModal"
        v-bind:roomKey="roomKey"
        v-bind:errorMsg="errorMsg"
        v-on:isopenmodalchanged="isOpenModal = !isOpenModal"
        v-on:set-errormsg="errorMsg = 'ルームキーが違います'"
        v-on:open-room="addRoom"
      ></roomModal>
    </template>

    <div class="chatContainer">
      <aside>
        <div class="aside-head">
          <h2>Chat</h2>
          <p>
            <span id="mark">●</span>
            {{ loginUser }}さん
          </p>
        </div>
        <div class="aside-body">
          <section>
            <b-list-group>
              <template v-for="room in rooms">
                <template v-if="room.addFlg === true">
                  <b-list-group-item
                    href="#"
                    id="b-list"
                    v-bind:class="{'flex-column': true, 'align-items-start': true}"
                    v-bind:key="room.roomId"
                    @click="room.roomKey != '' ? openModal(room) : addRoom(room);"
                  >
                    <div
                      v-bind:class="{'d-flex': true, 'w-100': true, 'justify-content-between': true, 'selected': roomIds.includes(room.roomId)}"
                    >
                      <h6 class="mb-1">
                        <!-- <h6 class="mb-1"> -->
                        <template v-if="room.roomKey === ''">
                          <b-badge variant="dark" pill>OPEN CHAT</b-badge>
                        </template>
                        <template v-else>
                          <b-badge variant="success" pill>PRIVATE CHAT</b-badge>
                          <b-icon icon="lock-fill"></b-icon>
                        </template>
                        <p>{{ room.roomName }}</p>
                        <small class="text-muted">
                          <span class="right-space">founder: {{ room.founder }}</span>
                        </small>
                      </h6>
                    </div>
                  </b-list-group-item>
                </template>
              </template>
            </b-list-group>
          </section>
        </div>
      </aside>
      <main class="chat">
        <section
          v-bind:class="{leftside: true, clicked:activeSection === 0}"
          @mouseover="activeSection = 0"
        >
          <div class="chat-head">
            <h2>{{roomName | string_format}}</h2>
          </div>
          <div class="chat-body">
            <message
              v-bind:chatList="this.sortedChatList"
              v-bind:userId="this.userId"
              v-on:update-good="updateGood"
            ></message>
            <textForm v-on:submit="postMessage"></textForm>
          </div>
        </section>

        <section
          v-show="roomIds.length === 2"
          v-bind:class="{rightside: true, clicked:activeSection === 1}"
          @mouseover="activeSection = 1"
        >
          <div class="chat-head">
            <h2>{{roomName2 | string_format}}</h2>
          </div>
          <div class="chat-body">
            <message
              v-bind:chatList="this.sortedChatList2"
              v-bind:userId="this.userId"
              v-on:update-good="updateGood"
            ></message>
            <textForm v-on:submit="postMessage"></textForm>
          </div>
        </section>
      </main>
    </div>
    <footer>
      <div class="chatFooter" @click="sendToRoom">Back To Rooms</div>
    </footer>
  </div>
</template>

<script>
import message from "./chat/message.vue";
import textForm from "./chat/textForm.vue";
import roomModal from "./roomModal.vue";
import firebase from "firebase";
import mixin from "../mixins.js";
import "../filter.js";

export default {
  name: "chat",
  props: [],
  components: {
    message: message,
    textForm: textForm,
    roomModal: roomModal,
  },

  data: function () {
    return {
      chatList: [],
      chatList2: [],
      userId: this.$route.query.userId,
      loginUser: this.$route.query.loginUser,
      profileImg: this.$route.query.profileImg,
      roomIds: [this.$route.query.roomId],
      roomName: this.$route.query.roomName,
      roomName2: "",
      rooms: this.$route.query.rooms,
      activeSection: 0,
      currentRoom: "",
      roomKey: "",
      inputKey: "",
      errorMsg: "",
      isOpenModal: false,
      activeRoom: "",
    };
  },

  mixins: [mixin],

  mounted: function () {
    //チャットを取得
    this.getMessagesFromDB();

    //下スクロール
    setTimeout(() => {
      var element = document.querySelector("section.leftside > div.chat-body");
      element.scrollTop = element.scrollHeight;
    }, 100);
  },

  methods: {
    /**
     *メッセージを投稿する
     *@param input 投稿メッセージ
     */
    postMessage: function (input) {
      //投稿メッセージのチェック処理
      if (input.length > 200 || input.length === 0) {
        return;
      }

      //Cloud Firestoreへ接続しメッセージを登録
      this.sendMessageToDB(input);

      //roomの最終更新日時を更新する
      this.updateDate();
    },

    /**
     * Cloud Firestoreへ接続しメッセージを登録する
     *@param input 投稿メッセージ
     */
    sendMessageToDB: function (input) {
      //messageIdを採番
      //ランダムな8桁英数 + 通番
      const messageId = this.getRandomString() + (this.chatList.length + 1);
      const name = this.loginUser;
      const img = this.profileImg;

      //登録するmessageオブジェクトを作成
      let message = {
        messageId: messageId,
        userId: this.userId,
        name: name,
        img: img,
        message: input,
        date: this.getCurrentDate(),
        goodList: [this.userId],
      };

      //Cloud Firestoreへ接続し、messageオブジェクトを登録
      firebase
        .firestore()
        .collection("rooms")
        .doc(this.roomIds[this.activeSection])
        .collection("chat")
        .doc(messageId)
        .set({ message })
        .then((doc) => {
          console.log(`${doc.id} added!`);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    /**
     *chatを取得する
     *Cloud Firestoreへ接続
     */
    getMessagesFromDB: function () {
      this.chatList = [];

      //Cloud FireStoreへ接続しchatを取得
      firebase
        .firestore()
        .collection("rooms")
        .doc(this.roomIds[this.activeSection])
        .collection("chat")
        .onSnapshot((snap) => {
          snap.docChanges().forEach((change) => {
            let data = "";
            //新規追加の場合
            if (change.type === "added") {
              console.log("added");
              data = change.doc.data();
              this.chatList.push(data.message);
              //変更の場合
            } else if (change.type === "modified") {
              console.log("modified");
              data = change.doc.data();
              //変更対象をいったん削除
              this.chatList = this.chatList.filter((chat) => {
                return chat.messageId !== data.message.messageId;
              });
              //変更後のmessageをリストに追加
              this.chatList.push(data.message);
            }
          });
        });
    },

    /**
     *chatを取得する（section2用）
     *Cloud Firestoreへ接続
     */
    getMessagesFromDBForSection2: function () {
      this.chatList2 = [];

      //Cloud FireStoreへ接続しchatを取得
      firebase
        .firestore()
        .collection("rooms")
        .doc(this.roomIds[this.activeSection])
        .collection("chat")
        .onSnapshot((snap) => {
          snap.docChanges().forEach((change) => {
            let data = "";
            //新規追加の場合
            if (change.type === "added") {
              console.log("added");
              data = change.doc.data();
              this.chatList2.push(data.message);
              //変更の場合
            } else if (change.type === "modified") {
              console.log("modified");
              data = change.doc.data();
              //変更対象をいったん削除
              this.chatList2 = this.chatList2.filter((chat) => {
                return chat.messageId !== data.message.messageId;
              });
              //変更後のmessageをリストに追加
              this.chatList2.push(data.message);
            }
          });
        });
    },

    /**
     * メッセージを最終更新日でソート
     * chatList用
     */
    sortByLastUpdate: function () {
      this.chatList = this.chatList.sort(function (a, b) {
        return a.date < b.date ? -1 : 1;
      });
    },
    /**
     * メッセージを最終更新日でソート
     * chatList2用
     */
    sortByLastUpdate2: function () {
      this.chatList2 = this.chatList2.sort(function (a, b) {
        return a.date < b.date ? -1 : 1;
      });
    },

    /**
     * 下スクロールする
     */
    scrollBottom: function () {
      // this.$nextTick(() => {
      setTimeout(() => {
        // window.scrollTo(0, document.querySelector(".chat").clientHeight);
      }, 300);
    },

    /**
     *roomの最終更新日を現在日時で更新する
     */
    updateDate: function () {
      firebase
        .firestore()
        .collection("rooms")
        .doc(this.roomIds[this.activeSection])
        .update({
          // last_modified: this.chatList[this.chatList.length - 1].date,
          last_modified: this.getCurrentDate(),
        })
        .then(() => {
          console.log(`${this.inputRoomName}roomを更新しました`);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    /**
     * room一覧へ戻る
     * 引継ぎ情報：userId,loginUser,profileImg
     */
    sendToRoom: function () {
      this.$router.push({
        path: "/room",
        query: {
          userId: this.$route.query.userId,
          loginUser: this.$route.query.loginUser,
          profileImg: this.$route.query.profileImg,
        },
      });
    },

    /**
     * いいねを更新する
     */
    updateGood: function (chat) {
      //自分の投稿の場合は何もしない
      if (chat.userId === this.userId) {
        return;
      }

      //すでにいいねしている場合
      if (chat.goodList.includes(this.userId)) {
        //自分のuserIdを除去する
        chat.goodList = chat.goodList.filter((userId) => {
          return userId !== this.userId;
        });
      } else {
        //まだいいねしていない場合は追加する
        chat.goodList.push(this.userId);
      }
      setTimeout(() => {
        //chatテーブルを更新する
        this.updateChat(chat);
      }, 80);
    },

    /**
     * Cloud Firestoreへアクセスし、chatを更新する
     */
    updateChat: function (message) {
      //Cloud FireStoreへ接続しメッセージを更新
      firebase
        .firestore()
        .collection("rooms")
        .doc(this.roomIds[this.activeSection])
        .collection("chat")
        .doc(message.messageId)
        .set({ message })
        .then(() => {
          console.log(`update goodList!`);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    /**
     *roomKey入力フォームを開く
     * @param room オブジェクト
     */
    openModal: function (room) {
      this.currentRoom = room;
      //入力されたroomの正誤判別のためroomKeyをセット
      this.roomKey = room.roomKey;

      this.errorMsg = "";

      //roomオブジェクトにroomKeyがない場合システムエラー
      if (this.roomKey === "") {
        alert("system error");
        return;
      }

      //モーダルを開く
      this.isOpenModal = !this.isOpenModal;
      //フォーカスをあてる
      //上スクロール
      this.scrollUp();
    },

    /**
     * 上スクロール
     */
    scrollUp: function () {
      window.scrollTo(0, 0);
    },

    /**
     *section2に新しいroomを追加する
     */
    addRoom: function (room) {
      //引数が空（=private room）の場合にはroomオブジェクトにcurrentRoomを設定
      if (room == "" || room == undefined) {
        room = this.currentRoom;
      }

      //roomNameを設定
      this.roomName2 = room.roomName;

      //roomIdsにroomId を追加
      if (this.roomIds.length == 1) {
        this.roomIds.push(room.roomId);
      } else {
        this.roomIds.splice(1, 1, room.roomId);
      }

      //アクティブセクションを変更
      this.activeSection = 1;

      //メッセージ一覧を取得
      this.getMessagesFromDBForSection2();

      //モーダルが開いている場合は閉じる
      if (this.isOpenModal) {
        this.isOpenModal = !this.isOpenModal;
      }

      //下スクロール
      setTimeout(() => {
        var element = document.querySelector(
          "section.rightside > div.chat-body"
        );
        element.scrollTop = element.scrollHeight;
      }, 100);
    },
  },
  computed: {
    //最終更新日でソートする
    sortedChatList: function () {
      let list = this.chatList;

      list.sort(function (a, b) {
        return a.date < b.date ? -1 : 1;
      });

      return list;
    },
    //最終更新日でソートする
    sortedChatList2: function () {
      let list = this.chatList2;

      list.sort(function (a, b) {
        return a.date < b.date ? -1 : 1;
      });

      return list;
    },
  },
};
</script>

