<template>
  <div class="container mt-5">
    <h2>パスワード変更画面</h2>
    <br />
    <template v-if="errorMsg !== ''">
      <span class="errorMsg">{{errorMsg}}</span>
      <br />
      <br />
    </template>
    <div class="row">
      <div class="col-sm-8">
        <form @submit.prevent="updatePassword">
          <div class="form-group">
            <label type="password">新しいパスワード：</label>
            <input type="password" class="form-control" id="password" v-model="password1" />
          </div>
          <div class="form-group">
            <label type="password">新しいパスワード（確認）：</label>
            <input type="password" class="form-control" id="password" v-model="password2" />
          </div>
          <button type="submit" class="btn btn-info">変更</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "updatePass",
  props: [],
  data: function () {
    return {
      password1: "",
      password2: "",
      isAuthenticated: true,
      errorMsg: "",
    };
  },

  methods: {
    /**
     * パスワード変更処理を行う
     */
    updatePassword: function () {
      //入力チェック
      if (this.checkForm()) {
        var user = firebase.auth().currentUser;
        user
          .updatePassword(this.password1)
          .then(() => {
            console.log("パスワード変更");
            alert("パスワード変更しました");
            this.$router.push({ path: "/" });
          })
          .catch(() => {
            alert("予期せぬエラーが起こりました");
          });
      }
    },

    /**
     * フォームチェックを行う
     * @return {boolean}
     */
    checkForm: function () {
      if (this.password1.length < 6) {
        this.errorMsg = "パスワードは6文字以上で入力してください";
        return false;
      }

      if (this.password1 !== this.password2) {
        this.errorMsg = "パスワードが一致しません";
        return false;
      }

      return true;
    },
  },
};
</script>

<style scoped>
.container {
  padding-top: 200px;
}

.errorMsg {
  color: red;
  font-size: 28px;
}
</style>
