<template>
  <div class="roomVue">
    <div id="profileModal" v-bind:class="{'hidden':!isOpenProfileModal}">
      <ul>
        <li @click="activeItem = 6;isOpenProfileModal = !isOpenProfileModal">
          <a>マイページ</a>
        </li>
        <li>
          <router-link to="/updateMail">メールアドレス変更</router-link>
        </li>
        <li>
          <router-link to="/updatePass">パスワード変更</router-link>
        </li>
      </ul>
    </div>
    <div
      id="mask"
      v-bind:class="{'hidden':!isOpenProfileModal}"
      @click="isOpenProfileModal = !isOpenProfileModal"
    ></div>
    <!-- RoomKey入力モーダル -->
    <!-- roomModal.vueのmountedを効かせるためにv-ifを使用 -->
    <template v-if="isOpenModal">
      <roomModal
        v-bind:roomKey="roomKey"
        v-bind:errorMsg="errorMsg"
        v-on:isopenmodalchanged="isOpenModal = !isOpenModal"
        v-on:set-errormsg="errorMsg = 'ルームキーが違います'"
        v-on:open-room="enterRoomForPrivateChat"
      ></roomModal>
    </template>
    <div class="rooms">
      <aside>
        <div class="aside-head">
          <h2>Rooms</h2>
          <p>
            <span id="mark">●</span>
            <a
              href="#"
              class="myPage"
              @click="isOpenProfileModal = !isOpenProfileModal"
            >{{ loginUser }}さん</a>
          </p>
          <div class="box">
            <div class="pic">
              <img
                v-bind:src="profile.profileImg"
                @click="isOpenProfileModal = !isOpenProfileModal"
              />
            </div>
            <div class="contents">
              <div class="text">
                <p>{{profile.comment}}</p>
              </div>
            </div>
          </div>
        </div>
        <div class="aside-body">
          <section>
            <div
              v-for="(menu, index) in filteredMenuList"
              @click="activeItem = index"
              v-bind:key="menu.number"
              v-bind:class="{ 'clicked': activeItem === index }"
            >
              <template v-if="menu.number !== 6">{{ menu.name }}</template>
            </div>
          </section>
        </div>
      </aside>

      <main>
        <div class="main-head">
          <template v-if="activeItem !== ''">
            <h2>{{ menuList[activeItem].name }}</h2>
            <div v-show="activeItem == 1 ||activeItem == 2 ||activeItem == 3">
              <b-form-group>
                <b-form-radio-group
                  id="btn-radios-1"
                  v-model="selected"
                  :options="options"
                  buttons
                  name="radios-btn-default"
                ></b-form-radio-group>
              </b-form-group>
            </div>
          </template>
        </div>
        <div class="main-body">
          <!-- Room作成タブ -->
          <roomCreation
            v-show="activeItem == 0"
            v-bind:errorMsg="this.errorMsg"
            v-bind:loginUser="this.loginUser"
            v-bind:userId="this.userId"
          ></roomCreation>

          <!-- Room一覧タブ -->
          <roomList
            v-show="activeItem == 1"
            v-bind:filteredRooms="this.filteredRooms"
            v-on:open-modal="openModal"
            v-on:send-to-chat="sendToChat"
            v-on:chageFavorites="chageFavorites"
          ></roomList>

          <!-- My Roomタブ -->
          <myRoom
            v-show="activeItem == 2"
            v-bind:filteredRooms="this.filteredRooms"
            v-bind:userId="this.userId"
            v-on:open-modal="openModal"
            v-on:send-to-chat="sendToChat"
            v-on:chageFavorites="chageFavorites"
            v-on:deleteRoom="deleteRoom"
          ></myRoom>

          <!-- favoritesタブ -->
          <favRoom
            v-show="activeItem == 3"
            v-bind:filteredRooms="this.filteredRooms"
            v-on:open-modal="openModal"
            v-on:send-to-chat="sendToChat"
            v-on:chageFavorites="chageFavorites"
          ></favRoom>

          <!-- chatbot -->
          <section v-show="activeItem == 4" class="chatbot">
            <chatbot
              v-bind:userId="this.userId"
              v-bind:loginUser="loginUser"
              v-bind:profileImg="profile.profileImg"
            ></chatbot>
          </section>

          <!-- メンバーリスト -->
          <memberList v-show="activeItem == 5" v-bind:userId="this.userId"></memberList>

          <!-- マイページ -->
          <myPage
            v-show="activeItem == 6"
            v-bind:profile="profile"
            v-on:updateProfileImg="updateProfileImg"
          ></myPage>
        </div>
      </main>
    </div>
    <footer></footer>
  </div>
</template>

<script>
import firebase from "firebase";
import mixin from "../mixins.js";
import chatbot from "./chat/chatbot.vue";
import roomList from "./room/roomList.vue";
import myRoom from "./room/myRoom.vue";
import favRoom from "./room/favRoom.vue";
import roomCreation from "./room/roomCreation.vue";
import myPage from "./myPage/myPage.vue";
import roomModal from "./roomModal.vue";
import memberList from "./room/memberList.vue";

export default {
  name: "room",
  props: [],
  components: {
    chatbot: chatbot,
    roomList: roomList,
    myRoom: myRoom,
    favRoom: favRoom,
    roomCreation: roomCreation,
    myPage: myPage,
    roomModal: roomModal,
    memberList: memberList,
  },
  data: function () {
    return {
      rooms: [],
      menuList: [
        {
          number: 0,
          name: "Create",
        },
        {
          number: 1,
          name: "Rooms",
        },
        {
          number: 2,
          name: "My Room",
        },
        {
          number: 3,
          name: "Favorites",
        },
        {
          number: 4,
          name: "To Question Chang",
        },
        {
          number: 5,
          name: "Members",
        },
        {
          number: 6,
          name: "My Page",
        },
      ],
      isOpenModal: false,
      isOpenProfileModal: false,
      // showRoomCreationFormFlag: false,
      roomKey: "",
      errorMsg: "",
      loginUser: this.$route.query.loginUser,
      currentRoom: "",
      activeItem: 1,
      userId: this.$route.query.userId,
      favRoomIds: [],
      options: [
        { text: "All", value: 0 },
        { text: "Private", value: 1 },
        { text: "Open", value: 2 },
      ],
      selected: "",
      profile: {
        profileImg: this.$route.query.profileImg,
        loginUser: this.$route.query.loginUser,
        userId: this.$route.query.userId,
        introduction: "",
        comment: "",
      },
    };
  },
  mixins: [mixin],

  created: function () {
    //初期化処理
    this.isOpenModal = false;
    this.roomKey = "";
    this.errorMsg = "";

    this.setFavRoomIds();
    //rooms取得
    this.getRoom();

    //profileテーブルから情報を取得する
    firebase
      .firestore()
      .collection("profile")
      .doc(this.userId)
      .get()
      .then((doc) => {
        if (doc.exists) {
          this.profile.introduction = doc.data().introduction;
          this.profile.comment = doc.data().comment;
          this.profile.profileImg = doc.data().photoURL;
        } else {
          console.log("profile未登録です");
          return;
        }
      })
      .catch((error) => {
        console.log(error.message);
        return;
      });
  },

  mounted: function () {
    this.selected = 0;
  },

  methods: {
    /**
     * Cloud Firestoreへ接続しroomオブジェクトを取得する
     */
    getRoom: function () {
      let rooms = [];

      //ルームリスト取得
      // let promise = new Promise((resolve) => {
      firebase
        .firestore()
        .collection("rooms")
        .orderBy("last_modified")
        .onSnapshot((snap) => {
          snap.docChanges().forEach((change) => {
            if (change.type === "added") {
              const room = change.doc.data();
              room.addFlg = false;
              rooms.push(room);
              console.log("room取得しました");
            }
            this.rooms = rooms;
          });
        });
    },

    /**
     * Cloud Firestoreへ接続しお気に入りroomIdを取得
     */
    setFavRoomIds: function () {
      //お気に入りルームリスト取得
      let favRoomIds = [];

      //お気に入りに登録されている部屋のidを取得する
      firebase
        .firestore()
        .collection("favorites")
        .doc(this.userId)
        .get()
        .then((doc) => {
          if (doc.exists) {
            favRoomIds = doc.data().fav_roomIds;
            if (favRoomIds === "" || favRoomIds.length === 0) {
              return;
            }

            //お気に入り登録判定に使用するためdataに持たせておく
            this.favRoomIds = favRoomIds;
          } else {
            console.log("fav_roomsが１つもありません");
            return;
          }
        })
        .catch((error) => {
          console.log(error.message);
          return;
        });
    },

    /**
     * chatへ画面遷移する
     * 引継ぎ情報：userId,loginUser,profileImg,roomId
     */
    sendToChat: function (room) {
      this.$router.push({
        path: "/chat",
        query: {
          userId: this.userId,
          loginUser: this.loginUser,
          profileImg: this.profile.profileImg,
          roomId: room.roomId,
          roomName: room.roomName,
          rooms: this.filteredRooms,
        },
      });
    },

    /**
     * chatへ画面遷移する(privateChatの場合)
     * 引継ぎ情報：userId,loginUser,profileImg,roomId
     */
    enterRoomForPrivateChat: function () {
      //エラーメッセージを初期化
      this.errorMsg = "";
      //チャット画面へ遷移
      this.sendToChat(this.currentRoom);
    },

    /**
     *入力されたroomkeyのチェック処理を行う
     */
    checkRoomKey: function () {
      if (this.inputKey !== this.roomKey) {
        return false;
      }
      this.roomKey = "";
      return true;
    },

    /**
     *roomKey入力フォームを開く
     * @param room オブジェクト
     */
    openModal: function (room) {
      //フォーム送信時に参照するためセットしておく
      this.currentRoom = room;
      //roomKeyをセット
      this.roomKey = room.roomKey;

      this.errorMsg = "";

      //roomオブジェクトにroomKeyがない場合システムエラー
      if (this.roomKey === "") {
        alert("system error");
        return;
      }

      //モーダルを開く
      this.isOpenModal = !this.isOpenModal;

      //上スクロール
      this.scrollUp();
    },

    /**
     * 上スクロール
     */
    scrollUp: function () {
      window.scrollTo(0, 0);
    },

    /**
     * お気に入りの追加または削除を行う
     * @param event イベントオブジェクト
     * @param room roomオブジェクト
     */
    chageFavorites: function (event, room) {
      //親要素へtのクリックイベント伝番を防ぐ
      event.stopPropagation();

      //クリックしたroomの表示切替
      room.addFlg = !room.addFlg;

      //Cloud FireStoreへ接続しお気に入りを更新
      this.updateFavorites();
    },

    /**
     * Cloud Firestoreへ接続し
     * お気に入りroomのroomIdを更新する
     */
    updateFavorites: function () {
      let fav_rooms = [];

      //DB登録のためお気に入りroomIdだけの配列を作成する
      //1,お気に入りroomリストを作成
      fav_rooms = this.rooms.filter((room) => {
        return room.addFlg === true;
      });
      //2,roomIdだけを切り出す
      this.favRoomIds = fav_rooms.map((room) => {
        return room.roomId;
      });

      //favRoomIds.length === 0だとroomがレンダリングされなくなるため
      if (this.favRoomIds.length === 0) {
        this.favRoomIds = [""];
      }

      //Cloud Firestoreに登録
      firebase
        .firestore()
        .collection("favorites")
        .doc(this.userId)
        .set({
          fav_roomIds: this.favRoomIds,
          uid: this.userId,
          useName: this.loginUser,
        })
        .then(() => {
          console.log(`update favorites!`);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    /**
     * roomを削除する
     * @param event イベントオブジェクト
     * @param room roomオブジェクト
     */
    deleteRoom: function (event, room) {
      //イベントが親要素へ伝番しないようにする
      event.stopPropagation();

      if (!window.confirm(`${room.roomName}を削除してもよろしいですか？`)) {
        return;
      }

      //room削除処理
      firebase
        .firestore()
        .collection("rooms")
        .doc(room.roomId)
        .delete()

        .then(function () {
          console.log(`${room.roomName} delteed!`);
        })
        .catch((error) => {
          console.log(error);
        });

      //画面を更新
      this.getRoom();
      this.setFavRooms();
    },

    updateProfileImg: function (newPhotoURL) {
      this.profile.profileImg = newPhotoURL;
      console.log("画像を更新しました");
    },
  },

  computed: {
    filteredRooms: function () {
      let filteredRooms = [];

      //未設定の場合はリターン
      //初期表示時に2回computedが動くのを防ぐため
      if (
        this.selected === "" ||
        this.favRoomIds.length === 0 ||
        this.rooms.length === 0
      ) {
        return;
      }

      //allの場合
      if (this.selected === 0) {
        filteredRooms = this.rooms;
      }
      //privateの場合
      if (this.selected === 1) {
        this.rooms.forEach((room) => {
          if (room.roomKey !== "") {
            filteredRooms.push(room);
          }
        });
      }
      //openの場合
      if (this.selected === 2) {
        this.rooms.forEach((room) => {
          if (room.roomKey === "") {
            filteredRooms.push(room);
          }
        });
      }

      //最終更新日でソートする
      filteredRooms = filteredRooms.sort(function (a, b) {
        return a.last_modified < b.last_modified ? 1 : -1;
      });

      //お気に入りroomの設定
      filteredRooms.forEach((room) => {
        if (this.favRoomIds.includes(room.roomId)) {
          room.addFlg = true;
        }
      });

      return filteredRooms;
    },

    //My Pageは一覧に表示しないので除去する
    filteredMenuList: function () {
      return this.menuList.slice(0, this.menuList.length - 1);
    },
  },
};
</script>


