<template>
  <section class="myPage">
    <div class="container">
      <div class="pic">
        <img :src="this.profile.profileImg" width="300" height="300" />
        <br />
        <div class="buttons">
          <input type="file" @change="onDrop" />
          <br />
          <button class="btn btn-info" @click="update">画像を変更する</button>
        </div>
      </div>
      <div class="contents">
        <div class="text">
          <p>{{profile.comment}}</p>
        </div>
      </div>
    </div>
    <div class="main">
      <p>UID:</p>
      <input type="text" class="form-control" v-model="profile.userId" disabled="true" />
      <p>Name：</p>
      <input type="text" class="form-control" v-model="profile.loginUser" />
      <p>Self-introduction:</p>
      <textarea
        type="textarea"
        class="form-control"
        v-model="profile.introduction"
        maxlength="200"
        rows="3"
        @input="checkRows"
      ></textarea>
      <p>Comment：</p>
      <input type="text" maxlength="50" class="form-control" v-model="profile.comment" />
      <button class="btn btn-info" @click="updateMyPage">更新</button>
    </div>
  </section>
</template>

<script>
import firebase from "firebase";

export default {
  name: "myPage",
  props: ["profile"],
  data: function () {
    return {
      fileList: "",
      uploadedImage: "",

      isDropingFlg: false, //ドロップ判定
    };
  },

  created: function () {},

  methods: {
    onDrop(event) {
      this.isDropingFlg = true;
      //画像データ取得
      let fileList = event.target.files || event.dataTransfer.files;
      this.fileList = fileList;

      this.createImage(fileList[0]);
    },
    // アップロードした画像を表示
    createImage(file) {
      let reader = new FileReader();
      reader.onload = (e) => {
        this.profile.profileImg = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    update() {
      if (this.isDropingFlg) {
        //ドロップした画像の種類の判定
        var errFlg;
        let blob;
        if (this.fileList[0].type == "image/png") {
          blob = new Blob(this.fileList, { type: "image/png" });
          errFlg = false;
        } else if (this.fileList[0].type == "image/jpg") {
          blob = new Blob(this.fileList, { type: "image/jpg" });
          errFlg = false;
        } else if (this.fileList[0].type == "image/jpeg") {
          blob = new Blob(this.fileList, { type: "image/jpeg" });
          errFlg = false;
        } else if (this.fileList[0].type == "image/gif") {
          blob = new Blob(this.fileList, { type: "image/gif" });
          errFlg = false;
        } else {
          errFlg = true;
        }

        if (!errFlg) {
          this.updateImage(blob);
        }
      }
    },
    updateImage(blob) {
      var storageRef = firebase.storage().ref();
      var mountainsRef = storageRef.child(`img/${this.fileList[0].name}`);
      //Firebase 画像アップロード
      mountainsRef.put(blob).then(() => {
        //アップロード完了
        mountainsRef
          .getDownloadURL()
          .then((url) => {
            //アップロードした画像の保存場所のURLを取得する
            this.updateAccount(url);
          })
          .catch((error) => {
            console.log(error);
          });
      });
    },
    /**
     * Cloud FireStoreへ接続し、プロフィール変更する
     */
    updateAccount(url) {
      firebase
        .firestore()
        .collection("profile")
        .doc(this.profile.userId)
        .update({
          photoURL: url,
        })
        .then(() => {
          this.$emit("updateProfileImg", url);
          alert("画像を変更しました");
        })
        .catch(() => {
          alert("予期せぬエラーが起こりました");
        });
    },

    /**
     *Cloud Firestoreへアクセスし、プロフィール情報を更新する
     */
    updateMyPage: function () {
      //Cloud Firestoreに登録
      firebase
        .firestore()
        .collection("profile")
        .doc(this.profile.userId)
        .set({
          uid: this.profile.userId,
          userName: this.profile.loginUser,
          introduction: this.profile.introduction,
          comment: this.profile.comment,
          photoURL: this.profile.profileImg,
        })
        .then(() => {
          alert("プロフィールを更新しました");
          console.log(`update favorites!`);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    checkRows: function () {
      const MAX_LINE_NUM = 5;

      let lines = this.profile.introduction.split("\n");
      console.log(lines.length);

      if (lines.length >= MAX_LINE_NUM) {
        let result = "";

        for (let i = 0; i < MAX_LINE_NUM; i++) {
          result += lines[i] + "\n";
        }

        this.profile.introduction = result;
      }
    },
  },
};
</script>

