<template>
  <div>
    <section id="modal">
      <form @submit.prevent="roomKey === inputKey ? $emit('open-room') : $emit('set-errormsg')">
        <div class="form-group">
          <div v-show="errorMsg !== ''" style="color: red">{{ errorMsg }}</div>
          <label type="password">ルームキーを入力してください：</label>
          <input
            type="password"
            class="form-control"
            id="password"
            v-model="inputKey"
            autofocus
            ref="focusThis"
          />
        </div>
      </form>
      <div id="close" @click="$emit('isopenmodalchanged')">閉じる</div>
    </section>
    <div id="mask"></div>
  </div>
</template>

<script>
export default {
  name: "roomModal",
  props: ["roomKey", "errorMsg"],
  data: function () {
    return {
      inputKey: "",
    };
  },
  mounted: function () {
    //フォーカスをあてる
    this.$refs.focusThis.focus();
  },
};
</script>

