<template>
  <div class="container mt-5">
    <h2>メールアドレス変更画面</h2>
    <br />
    <template v-if="errorMsg !== ''">
      <span class="errorMsg">{{errorMsg}}</span>
      <br />
      <br />
    </template>
    <div class="row">
      <div class="col-sm-8">
        <form @submit.prevent="updatePassword">
          <div class="form-group">
            <label for="email">現在のメールアドレス：</label>
            <input
              type="email"
              class="form-control"
              id="email"
              v-model="currentMail"
              disabled="true"
            />
          </div>
          <div class="form-group">
            <label for="email">新しいメールアドレス：</label>
            <input type="email" class="form-control" id="email" v-model="email" autocomplete="off" />
          </div>
          <button type="submit" class="btn btn-info">変更</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "updateMail",
  props: [],
  data: function () {
    return {
      currentMail: "",
      email: "",
      isAuthenticated: true,
      errorMsg: "",
    };
  },

  mounted: function () {
    var user = firebase.auth().currentUser;
    this.currentMail = user.email;
  },

  methods: {
    /**
     * パスワード変更処理を行う
     */
    updatePassword: function () {
      //入力チェック
      if (this.checkForm()) {
        var user = firebase.auth().currentUser;

        // Clouod FireStoreへ接続し、メールアドレスを変更する
        user
          .updateEmail(this.email)
          .then(() => {
            console.log("メールアドレス変更");
            alert("メールアドレス変更しました");
            this.$router.push({ path: "/" });
          })
          .catch(() => {
            alert("予期せぬエラーが起こりました");
          });

        //メール送信処理
        // user
        //   .sendEmailVerification(this.email)
        //   .then(function () {
        //     alert(`${this.email}にメールを送信しました`);
        //   })
        //   .catch(function (error) {
        //     console.log(error);
        //   });
      }
    },

    /**
     * フォームチェックを行う
     * @return {boolean}
     */
    checkForm: function () {
      if (this.email === "") {
        this.errorMsg = "メールアドレスが入力されていません";
        return false;
      }
      return true;
    },
  },
};
</script>

<style scoped>
.container {
  padding-top: 200px;
}

.errorMsg {
  color: red;
  font-size: 28px;
}
</style>