<template>
  <div class="container mt-5">
    <h2>新規登録</h2>
    <br />
    <span class="errorMsg">{{errorMsg}}</span>
    <br />
    <div class="row">
      <div class="col-sm-8">
        <form @submit.prevent="signUp">
          <div class="form-group">
            <label for="displayName">ニックネーム：</label>

            <input
              type="text"
              class="form-control"
              placeholder="ちゃん・かわうそ"
              v-model="userName"
              autocomplete="off"
            />
          </div>

          <div class="form-group">
            <label for="email">メールアドレス：</label>
            <input type="email" class="form-control" id="email" v-model="email" autocomplete="off" />
          </div>
          <div class="form-group">
            <label type="password">パスワード：</label>
            <input type="password" class="form-control" id="password" v-model="password" />
          </div>
          <div class="form-group">
            <label type="password">パスワード(確認)：</label>
            <input type="password" class="form-control" id="password" v-model="password2" />
          </div>
          <button type="submit" class="btn btn-info">登録</button>
        </form>
        <p>
          <router-link to="/">サインインはこちら</router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
// import defaultImg from "@/assets/cat.png";

export default {
  name: "singUp",
  props: [],
  data: function () {
    return {
      email: "",
      password: "",
      password2: "",
      userName: "",
      isAuthenticated: false,
      errorMsg: "",
    };
  },

  mounted: function () {
    this.errorMsg = "";
  },

  methods: {
    /**
     * Cloud Firestoreへ接続しユーザー情報を登録する
     * 登録内容：email password displayName photoURL
     */
    signUp: function () {
      //入力チェック
      if (!this.checkForm()) {
        return;
      }

      //Cloud Firestoreへユーザー情報を登録する
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then((result) => {
          result.user.updateProfile({
            displayName: this.userName,
          });

          //プロフィールTBLを作成する
          this.createProfile(result.user.uid);

          //お気に入りTBLを作成する
          this.createFavorites(result.user.uid);

          alert("アカウントを作成しました");
          this.$router.push("/");
        })
        .catch((error) => {
          this.errorMsg = `システムエラー：${error.message}`;
        });
    },

    //プロフィールを作成する
    createProfile: function (uid) {
      //Cloud Firestoreに登録
      firebase
        .firestore()
        .collection("profile")
        .doc(uid)
        .set({
          uid: uid,
          userName: this.userName,
          introduction: "",
          comment: "こんにちは",
          photoURL:
            "https://firebasestorage.googleapis.com/v0/b/chatapp-fe0b0.appspot.com/o/img%2Fcat.png?alt=media&token=7b27d4f2-9248-4182-9ab7-89aeae9694f6",
        })
        .then(() => {
          console.log("created profile");
        })
        .catch((error) => {
          console.log(error);
        });
    },

    //お気に入りTBLを作成する
    //Cloud Firestoreに登録
    createFavorites: function (uid) {
      firebase
        .firestore()
        .collection("favorites")
        .doc(uid)
        .set({
          fav_roomIds: [""],
          uid: uid,
          useName: this.userName,
        })
        .catch((error) => {
          console.log(error);
        });
    },

    /**
     * フォームチェックを行う
     * @return {boolean}
     */
    checkForm: function () {
      if (this.userName.length < 2) {
        this.errorMsg = "名前は2文字以上で入力してください";
        return false;
      }

      if (this.email === "") {
        return false;
      }

      if (this.password.length < 6) {
        this.errorMsg = "パスワードは6文字以上で入力してください";
        return false;
      }

      if (this.password !== this.password2) {
        this.errorMsg = "パスワードが一致しません";
        return false;
      }

      return true;
    },
  },
};
</script>

<style scoped>
.container {
  padding-top: 200px;
}

.errorMsg {
  color: red;
  font-size: 28px;
}
</style>
