<template>
  <section>
    <b-list-group>
      <template v-for="profile in profileList">
        <b-list-group-item
          href="#"
          id="b-list"
          class="flex-column align-items-start list"
          v-bind:key="profile.uid"
        >
          <p class="mb-1">{{profile.userName}}</p>
          <section class="container">
            <div class="subheading">
              <img v-bind:src="profile.photoURL" />
              <div class="text">{{profile.comment}}</div>
            </div>
            <div class="contents">{{profile.introduction}}</div>
          </section>
        </b-list-group-item>
      </template>
    </b-list-group>
  </section>
</template>

<script>
import firebase from "firebase";

export default {
  name: "memberList",
  props: ["userId"],
  data: function () {
    return {
      profileList: [],
    };
  },
  created: function () {
    //profileテーブルから情報を取得する
    firebase
      .firestore()
      .collection("profile")
      .onSnapshot((snap) => {
        snap.docChanges().forEach((change) => {
          const data = change.doc.data();

          //追加の場合
          if (change.type === "added") {
            this.profileList.push(data);
            //変更の場合
          } else if (change.type === "modified") {
            //変更対象をいったん削除
            this.profileList = this.profileList.filter((profile) => {
              return profile.uid !== data.uid;
            });
            this.profileList.unshift(data);
          }
        });
      });
    // .get()
    // .then((snapshot) => {
    //   if (snapshot.empty) {
    //     console.log("No matching documents.");
    //     return;
    //   }
    //   snapshot.forEach((doc) => {
    //     // if (doc.data().uid !== this.userId) {
    //     this.profileList.push(doc.data());
    //     // }
    //   });
    // })
    // .catch((err) => {
    //   console.log("Error getting documents", err);
    // });
  },
};
</script>

